.formulario{
  display: flex;
  justify-content: space-evenly;
  margin: 80px 50px;
  flex-wrap: wrap;
  gap: 20px;
}

.formulario form{
  background-color: #f7f7f7;
  border-radius: 20px;
  box-shadow: 7px 7px 15px 0px #00000014;
  padding: 8px 100px;
  flex: 1;
}

.formulario h2{
  font-size: 32px;
  font-weight: 400;
  color: #212121;
}